@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
:root {
  /* Colors primary */
  --cyan: #26C3AC;
  --bg_color: #C5E5E6;
  --white: #FFFEFE;
  --gray: #F2F8FA;
  --gray_full: #808080;
  --green: #00464A;
  --orange: #da4f21;

  /* Fonts */
  --font-family: 'Space Mono', monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Space Mono', monospace;
  font-family: var(--font-family);
  font-size: 16px;
  background-color: #C5E5E6;
  background-color: var(--bg_color);
}

/* Global css */
.container__root {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.label {
  color: #00464A;
  color: var(--green);
  font-size: 1rem;
  font-weight: 700;
}
.label__warning {
  color: #da4f21;
  color: var(--orange);
}
.label__white {
  color: #FFFEFE;
  color: var(--white);
  font-weight: 400;
}
.label__cyan {
  color: #26C3AC;
  color: var(--cyan);
  font-size: 1.4rem;
}
.label__gray {
  color: #808080;
  color: var(--gray_full);
  font-weight: 400;
}
.button {
  height: 3.1rem;
  margin-top: 1.2rem;
  font-size: 1.1rem;
  font-weight: 700;
  background-color: #26C3AC;
  background-color: var(--cyan);
  color: #00464A;
  color: var(--green);
  border: none;
  border-radius: 5px;
  padding: 0.8rem 1rem;
  outline: none;
  cursor: pointer;
}
.button:hover {
  background-color: #F2F8FA;
  background-color: var(--gray);
  transition: 0.5s ease-in-out;
}
.button:active {
  -webkit-transform: scale(.9);
          transform: scale(.9);
}
.hide {
  display: none;
}
@media screen and (min-width: 1024px) {
  .container__root {
    height: 100vh;
    justify-content: center;
  }
  .label {
    font-size: 0.9rem;
  }
  .label__cyan {
    color: #26C3AC;
    color: var(--cyan);
    font-size: 1.9rem;
  }
}
.img__logo {
  margin: 2rem 0;
}
.calculator {
  display: flex;
  flex-direction: column;
  grid-gap: 2rem;
  gap: 2rem;
  max-width: 1000px;
  width: 100%;
  background-color: var(--white);
  border-radius: 20px;
  box-shadow: 0px 1px 0px 0.8px rgba(0, 0, 0, 0.15);
  padding: 1rem;
}
.select__tip {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  grid-gap: 6px;
  gap: 6px;
  justify-content: space-between;
  padding: 1rem 0px;
}
.totals__wrapper {
  display: grid;
  grid-auto-flow: row;
  align-content: space-between;
  grid-gap: 10px;
  gap: 10px;
  margin-top: 1rem;
  padding: 2rem;
  background-color: var(--green);
  border-radius: 10px;
}
@media screen and (min-width: 500px) {
  .img__logo {
    width: 15%;
  }
}
@media screen and (min-width: 768px) {
  .calculator {
    flex-direction: row;
    justify-content: space-between;
  }
  .totals__wrapper {
    width: 50%;
  }
}
@media screen and (min-width: 1024px) {
  .calculator {
    max-width: 700px;
  }
  .img__logo {
    width: 5%;
  }
}


.bill__amount {
  display: grid;
  grid-gap: 5px;
  gap: 5px;
  margin-bottom: 1rem;
}
.bill__input {
  font-family: var(--font-family);
  font-size: 1.3rem;
  font-weight: 700;
  text-align: right;
  padding: 0.5rem;
  max-width: 900px;
  width: 100%;
  border: none;
  border-radius: 5px;
  background-color: var(--gray);
  background-image: url(/calculator-of-tip/static/media/icon-dollar.9f9be353.svg);
  background-repeat: no-repeat;
  background-position-x: 20px;
  background-position-y: center;
  color: var(--green);
  outline: none;
}
.bill__input:focus {
  border: 1.5px solid var(--cyan);
  transition: border 0.3s ease-in-out;
}
.tip__button {
  max-width: 180px;
  width: 47%;
  height: 3.1rem;
  background-color: var(--green);
  color: var(--white);
  border: none;
  border-radius: 8px;
  outline: none;
  box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.15);
}
.tip__button:active {
  -webkit-transform: scale(.9);
          transform: scale(.9);
  background-color: var(--cyan);
}
.tip__input {
  font-family: var(--font-family);
  font-size: 1.3rem;
  font-weight: 700;
  text-align: center;
  padding: 0.5rem;
  max-width: 180px;
  width: 47%;
  height: 3.1rem;
  border: none;
  border-radius: 5px;
  background-color: var(--gray);
  color: var(--green);
  outline: none;
}
.tip__input:focus {
  border: 1.5px solid var(--cyan);
  text-align: right;
  transition: 0.3s ease-in-out;
}
@media screen and (min-width: 1024px) {
  .tip__button {
    width: 30%;
    height: 2.5rem;
  }
  .tip__input {
    width: 30%;
    height: 2.5rem;
    font-size: 1rem;
  }
}
.people__wrapper {
  margin-top: 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "title title" "warning warning" "input input";
  grid-gap: 10px;
  gap: 10px;
}
.input {
  font-family: var(--font-family);
  font-size: 1.3rem;
  font-weight: 700;
  text-align: right;
  padding: 0.5rem;
  max-width: 900px;
  width: 100%;
  border: none;
  border-radius: 5px;
  background-color: var(--gray);
  background-image: url(/calculator-of-tip/static/media/icon-person.65bfd204.svg);
  background-repeat: no-repeat;
  background-position-x: 20px;
  background-position-y: center;
  color: var(--green);
  outline: none;
  grid-area: input;
}
.input:focus {
  border: 1.5px solid var(--cyan);
  transition: border 0.3s ease-in-out;
}
.input__warning {
  border: 1.5px solid var(--orange);
  transition: border 0.3s ease-in-out;
}
.input__warning:focus {
  border: 1.5px solid var(--orange);
  transition: border 0.3s ease-in-out;
}
.grid__title {
  grid-area: title;
}
.grid__warning {
  grid-area: warning;
}
@media screen and (min-width: 768px) {
  .people__wrapper {
    grid-template-areas: "title warning" "input input";
  }
}
.tipAmount__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tipAmount__wrapper {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
}

.total__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.total__wrapper {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
}
